@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500&display=swap);
:root {
    --black:  #000000;
    --white: #ffffff;
    --purple: #26225e;
    --pink: #FF6EC7;
    --gray: #7f7f7f;
    --box-shadow: 0px 0px 10px 0px rgba(34, 33, 33, 0.2);
    --family: 'Montserrat', sans-serif;
}

* {
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    font-family: var(--family);
    font-weight: bold;
    margin: 0;
    padding: 0;
}

body {
    background: #ffffff;
    background: var(--white);
    padding-top: 40px;
}

button {
    background-color: transparent;
    border: transparent;
    border: 2px solid #000000;
    border: 2px solid var(--black);
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
    color: #000000;
    color: var(--black);
    margin: 0 2px;
    width: 50px;
    height: 30px;
    outline: none;
}

a {
    color: #000000;
    color: var(--black);
    text-decoration: none;
}

a:hover {
    color: #26225e;
    color: var(--purple);
}

svg {
    display: block;
    margin: 20px auto 0px;
    overflow: hidden;
}

img {
    width: 70px;
    height: 70px;
}

h1 {
    font-size: 22px;
    display: inline;
}

h2 {
    font-size: 20px;
    text-align: center;
}

.container {
    box-shadow: 0px 0px 10px 0px rgba(34, 33, 33, 0.2);
    box-shadow: var(--box-shadow);
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
}

@media only screen and (min-width: 600px) {
    body {
        padding-top: 100px;
    }

    h1 {
        font-size: 30px;
    }
}

.buttons {
    align-items: center;
    display: flex;
    justify-content: flex-end;
    width: 100%;
    padding-right: 20px;
}

.active {
    border: 2px solid var(--pink);
    color: var(--pink);
}

@media only screen and (min-width: 600px) {
    .buttons {
        padding-right: 50px;
    }
}

.title {
    align-items: center;
    display: flex;
    justify-content: center;
}

.info {
    position: relative;
}

.i {
    background-color: var(--pink);
    border-radius: 50%;
    color:var(--black);
    cursor: pointer;
    font-style: italic;
    font-size: 10px;
    font-weight: bold;
    width: 14px;
    height: 14px;
    line-height: 16px;
    margin: 16px 0 0 4px;
    padding-right: 2px;
    text-align: center;
}

p {
    color: var(--gray);
    font-size: 10px;
    font-weight: 100;
    line-height: 12px;
    margin-top: 6px;
}

.note {
    border: 2px solid var(--pink);
    background-color: var(--white);
    border-radius: 8px;
    padding: 0px 6px 6px 6px;
    position: absolute;
    right: 0;
}

.show {
    display: block;
}

.hide {
    display: none;
}

.bar_back {
    fill: var(--pink);
    stroke: transparent;
    stroke-width: 2px;
}

.bar_front {
    fill: transparent;
    stroke: var(--black);
    stroke-width: 2px;
}

.bar_label {
    fill: var(--black);
    font-size: 14px;
    font-weight: 600;
    text-anchor: middle;
}

.xAxis-label,
.yAxis-label {
    fill: var(--gray);
    font-size: 12px;
    font-weight: 500;
    text-anchor: middle;
}

.domain {
    stroke-width: 2px;
}

.tick line {
    stroke: transparent;
}

.tick text {
    font-family: var(--family);
    font-size: 10px;
    font-weight: bold;
}

@media only screen and (min-width: 600px) {
    .xAxis-label,
    .yAxis-label {
        font-size: 14px;
    }

    .tick text {
        font-size: 12px;
    }
}

.link {
    margin-top: 20px;
    text-align: right;
}

.logo {
    width: 14px;
    height: 14px;
    margin: 0 2px;
}

