.buttons {
    align-items: center;
    display: flex;
    justify-content: flex-end;
    width: 100%;
    padding-right: 20px;
}

.active {
    border: 2px solid var(--pink);
    color: var(--pink);
}

@media only screen and (min-width: 600px) {
    .buttons {
        padding-right: 50px;
    }
}
